
<vue-slider-component
  class="s-slider"
  :class="{
    's-slider--simple': simpleTheme,
    's-slider--has-tooltip': tooltip === 'always',
  }"
  :width="width"
  :height="8"
  :dot-size="[24, 16]"
  :tooltip="tooltip"
  tooltip-placement="bottom"
  :min="min"
  :max="max"
  :interval="interval"
  :value="displayValue"
  :tooltip-formatter="prefix + '{value}' + suffix"
  :data="data"
  :disabled="disabled"
  v-bind="$attrs"
  v-on="$listeners"
  @change="(value) => emitInput(value)"
  ref="slider"
/>
